@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans:700&display=swap);
body{font-family:"Lato",sans-serif;background:#86b9e0}h1{margin:0}.wrapper{background:#86B9E0;height:100vh;display:flex;justify-content:center;align-items:center}.wrapper .mainWrapper{height:90vh;background:transparent;width:80%;margin:0 auto}@media (max-width: 767px){.wrapper .mainWrapper{height:50vh}}.wrapper .innerWrapper{max-width:1170px;padding-right:15px;padding-left:15px;margin-right:auto;margin-left:auto}.wrapper .weatherDesc{font-family:"Alegreya Sans",sans-serif;max-width:800px;display:block;margin:0 auto}input[type="text"]{height:50px;border:2px solid #fff;border-radius:8px;width:60%;max-width:100%;font-size:16px}@media (max-width: 767px){input[type="text"]{width:100%;margin-bottom:20px}}button{height:50px;margin:0 30px;background:#86b9e0 !important;color:#fff;border:2px solid #fff;font-size:1em;line-height:20px;cursor:pointer}@media (min-width: 768px){button{float:right}}button:hover{background:#fff !important;color:#86b9e0 !important}.titleHeader{font-size:32px;font-weight:bold;color:#fff;text-align:center}.subTitle{font-size:24px;font-weight:bold;color:#fff;text-align:center}.citySearch{display:block;margin:0 auto;text-align:center;position:relative;padding:0 14%}@media (max-width: 767px){.citySearch{padding:0 !important}}.container{text-align:center;color:#fff;box-shadow:0px 13px 40px -13px rgba(0,0,0,0.75);padding:20px;background:rebeccapurple;border-radius:8px;max-width:400px;display:block;margin:0 auto;top:30px;position:relative}.container .city{font-family:"Alegreya Sans",sans-serif}.container .city .temperature{font-size:3em;line-height:1.3}.container .city .description{text-transform:uppercase;font-family:"Lato",sans-serif}.container .city .icon{width:200px;height:200px}.container .city #weatherInfoDescription{padding-bottom:30px}.container .city #weatherInfoError{color:#fff;padding:1em;font-size:24px}

